////
/// gurka navigation main
/// @group module
////

/// Description
//
/// Description

nav {

	padding: 0;

	&.main {
		position: fixed;
		left: -70%;
		top: 0;
		width: 70%;
		height: 100%;
		z-index: 9999;
		background: $color-gray-light;
		overflow: hidden;
		transition: left 0.5s;



	@include media($screen-sm) {
		@include outer-container;
		display: block;
		position: relative;
		left: 0px;
		width: 100%;
		margin-bottom: $margin-default;
		overflow: hidden;
		overflow-y: auto;
		background: transparent;
	}



		ul {
		padding: $padding-default;
		margin-top: $header-wrapper-height;

		@include media($screen-sm) {
			display: flex;
			justify-content: center;
			padding: 0;
			margin-top: 0;
			//background-color: $color-primary;
			background-color: $color-white;
			border-bottom: 1px solid $color-secondary;
			margin-bottom: 0;
		}

		}

		li {

		padding: ($padding-default) 0;

		@include media($screen-sm) {
			display: inline;
			padding: $padding-default /2;
		}

			margin-bottom: 0;

			a {
				@include media($screen-sm) {
					color: $color-button-primary;
					color: $color-secondary;
				}

			}
		}

	}

}




.menu-trigger {
	@extend .button;
	@include span-columns(2);
	@include clearfix();
	background: $color-secondary;
	width: $navi-trigger-width;
}






body.showmenu  {
	max-height: 100%;
	overflow-x: hidden;
	overflow-y: hidden;

	.menu-trigger:after {
		content: "\f00d"; // fa-erase
	}

}

body >*:not(nav) {
	position: relative;
	left: 0;
	transition: left 0.5s;

}

body.showmenu >*:not(nav) {
	left: 73%;
	transition: left 0.5s;

	@include media($screen-sm) {
	left: 0;
	}

}

body.showmenu {
	nav.main {
		left: 0px;
		display: block;
		overflow: hidden;
		overflow-y: auto;
	}
}
