////
/// gurka Coach
/// @group module
////

/// Coach

.coach {
	@include row();

	&-name {
		@extend .button;
		@include font-size(22px);
		text-align: left;
		display: block;
		font-family: $font-medium;
		color: $color-secondary;
		border: 1px solid $color-secondary;

		&[class*="icon-"]:before {
			position: relative;
			top: 2px;
			color: inherit;
			font-family: $font-icon;
			speak: none;
			line-height: 1;
			display: inline-block;
		}
		&.icon-coach:before {
			content: "\f007"; // fa-user
			@include font-size(26px);
		}

	}


	&-row {
		@include row();
		align-items: center;
		margin-bottom: $margin-default *2;

		@include media($screen-sm) {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: $margin-default
		}

		&:last-child {
			margin-bottom: -$margin-default;
		}

	}

	&-col {
		margin-bottom: $margin-default;

		&.one {

			@include media($screen-sm) {
				@include span-columns(12);
			}

		}

		&.two {

			@include media($screen-sm) {
				@include span-columns(6);
				@include omega(2n);

				&.shift-right {
					@include shift(6);
				}

			}

		}

		&.three {

			@include media($screen-sm) {
				@include span-columns(4);
				@include omega(3n);

				&.shift-right {
					@include shift(8);
				}

				&.shift-middle {
					@include shift(4);
				}

			}

		}

		&.four {

			@include media($screen-sm) {
				@include span-columns(3);
				@include omega(4n);

				&.shift-right {
					@include shift(9);
				}

			}

		}

		&.two-third {

			@include media($screen-sm) {
				@include span-columns(9);
				@include omega(0);

				&.shift-right {
					@include shift(3);
				}

			}

		}



	}

	.btn {
		display: block;
	}


}
