////
/// gurka Coach
/// @group module
////

/// Rating



.player-list li[data-player] {
	display: inline-block;
}





*[data-player] strong{
	display: inline-block;
	background: $color-secondary;
    border: 1px solid $color-gray-medium;
	color: $color-white;

    border-radius: $border-radius;
    padding: $padding-btn;
    margin: 0 $margin-default/3 $margin-default/3 0;
    cursor: pointer;
    font-weight: normal;
	@include font-size(13px);

@include media($screen-sm) {
	@include font-size(14px);
}

}



.rating {

	&-scale {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		flex-wrap: wrap;

	@include media($screen-sm) {
		flex-wrap: nowrap;
	}

		&-container {
			margin: 0.5em 0;
			border: 1px solid $color-gray-medium;
			border-radius: 0 0 4px 4px;
			padding: $margin-default $margin-default/2 $margin-default/2;
			margin-top: calc(-#{$margin-default} - 2px);

			margin-bottom: $margin-default;
			border-top: 0;
		}

		label {
			height: 34px;
			text-align: center;
			color: #fff;
			line-height: 34px;
			display: inline-block;
			flex-grow: 1;
			flex-basis: 1;
			cursor: pointer;
			position: relative;
			max-width: 17%;

			border: 1px solid $color-gray-medium;
			background: $color-white;
			color: $color-gray-medium;

			&:before,
			&:after {
				display: none !important;
			}

		&.selected {
			border: 1px solid $color-gray-medium;
			background: $color-secondary;
			color: $color-white;
		}


		}

		&-headline {
			margin: 1em 0;

			&:after {
				content: '';
				font-family: "FontAwesome";
				font-size: 16px;
				width: 16px;
				display: inline-block;
				margin-left: 10px;
				color: #c00;

			}

			&.headline-techinque:after {
				content: '\f0d0';
			}
			&.headline-tactics:after {
				content: '\f047';
			}
			&.headline-teamplay:after {
				content: '\f0c0';
			}



		}

	}

	&-container {
		margin: 0 0 $margin-default 0;
	}

	&-row {
		display: flex;
		margin: 0 0 $margin-default 0;
	}

	&-col {
		width: calc(100% - 0.75em);
		margin: 0 $margin-default/2;
		padding: $padding-default/3;


	&.two {
		width: calc(50% - #{$margin-default});
		padding-bottom: 0;
		/*border: 1px solid $color-gray-medium;
		border-radius: $border-radius;*/
		box-shadow: $box-shadow;
	}

	}




	h3.matchside-headline span {
		justify-content: center;
		background: transparent;
		border: 0;

		@extend .design-col-b;

		cursor: default;
		margin: 0 0 $margin-default/2 0;
		width: 100%;
		@include font-size(13px);
		@include media($screen-sm) {
			@include font-size(16px);
		}
		height: 30px;
		display: flex;
		align-items: center;
	}


	*[data-matchside] *[data-player] strong {
		justify-content: center;
		background: transparent;
		border: 0;
		flex-basis: 100%;

		@extend .design-col-b;

		cursor: default;
		margin: 0 $margin-default $margin-default;
		width: 100%;
		@include font-size(13px);
		display: flex;
		align-items: center;
		@include media($screen-sm) {
			@include font-size(16px);

		}

	}

@include media($screen-sm) {
	*[data-matchside] *[data-player] strong {
		width: auto;
		margin-bottom: 0;
	}
}

}
