

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

// Box sizing
*,
*:before,
*:after {
	box-sizing: border-box;
}

// Focus outline
a:focus {
	outline: thin dotted;
}

// List
ol, ul {
	list-style: none;
}

// Separator
hr {
	width: 100%;
	border: none;
}

// Typography
b,
strong {
	font-weight: bold;
}

// Image
img {
	width: 100%;
	height: auto;
	display: block;
}

// Iframe
iframe {
	width: 100%;
	border: 0;
	overflow: hidden;
}

// Form element
select,
button,
textarea,
input[type="tel"],
input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"],
input[type="submit"],
input[type="button"] {
	width: 100%;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	appearance: none;
}

input[type="radio"],
input[type="checkbox"] {
	position: absolute;
	left: -9999px;
}

// Placeholder
::-webkit-input-placeholder {
	color: gray;
}

::-moz-placeholder {
	color: gray;
}

:-ms-input-placeholder {
	color: gray;
}

// MS IE
select::-ms-expand,
input::-ms-check {
	visibility: hidden;
}

// Firefox
@-moz-document url-prefix() {
	select, select:-moz-focusring,
	select::-moz-focus-inner {
		color: transparent !important;
		text-shadow: 0 0 0 #000 !important;
	}
}
