.news-aside {

	.news-teaser {
		margin-bottom: $margin-default;
		display: flex;
		flex: 1;
		flex-wrap: wrap;

		li {
			@include span-columns(6);
			@include omega (2n);
			flex-basis: 50%;
			flex-wrap: wrap;


			@include media($screen-sm) {
				@include span-columns(12);
				@include omega-reset(2n);
				@include omega (1n);
				flex-basis: 100%;
				margin-bottom: $margin-default *2;
			}

			figure {
				text-align: left;
			}

			p {

			}
		}

	}


	*[data-frontend="panel-headline"] + .news-teaser {
		display: none;
	}

	@include media($screen-sm) {
		*[data-frontend="panel-headline"] + .news-teaser {
			display: flex !important;
		}
	}

}
