

// Font
$font-base: "UbuntuRegular", Arial, Helvetica, sans-serif;
$font-light: "UbuntuLight", Arial, Helvetica, sans-serif;
$font-regular: "UbuntuRegular", Arial, Helvetica, sans-serif;
$font-medium: "UbuntuMedium", Arial, Helvetica, sans-serif;
$font-bold: "UbuntuBold", Arial, Helvetica, sans-serif;

// Icon
$font-icon: "FontAwesome";

// Color scheme
$color-primary: #ff2404;
$color-secondary: #1485CC;
$color-tertiary: #54CC14;
$color-complementary: #ffd600;

// Basic colors
$base-background-color: #fff !default;
$base-font-color: #333;
$color-white: #fff;
$color-black: #000;
$color-green: #23d600;

$color-gray-dark: #333;
$color-gray-medium: #ddd;
$color-gray-light: #f7f7f7;

//Action Color
$action-color: $color-primary !default;
$color-field-active: $color-secondary;

// Error
$color-error: red;
$color-warning: orange;

// Button
$color-button-primary: #fff;
$color-button-secondary: #fff;
$color-button-tertiary: #fff;

// Box-Shadow
$box-shadow: 0 2px 2px 0 rgba(55, 55, 55, 0.2);
$box-shadow2: 1px 1px 1px 1px rgba(55,55,55,0.25);
//Border
$base-border-color: $color-gray-medium;

//Space

$base-spacing: 1em !default;

@include media($screen-sm) {
  $base-spacing: 1.5em !default;
}

// Distance-default
$distance-default: $base-spacing /2;

// Margin
$margin-default: $distance-default;

$margin-gutter: $gutter;
$margin-gutter-xs: $margin-gutter;

// Padding
$padding-default: $distance-default;
$padding-vertical-default: ($distance-default) 0;
$padding-btn: ($distance-default) /1.5 ($padding-default) /1.5;

// Border radius
$border-radius: 4px !default;

// Opacity
$opacity-low: 0.8;
$opacity-medium: 0.5;
$opacity-high: 0.2;

// Header
$header-wrapper-height: 120px;

// If Navigation - Trigger and Searchfield
$navi-trigger-width: 32px;
$main-search-width: ($column * $grid-columns +  $gutter * $grid-columns - $gutter) / $max-width *99.9%;
