/// Be aware of element reset
/// @see: reset.scss

fieldset {
	width: 100%;
}

legend {
	@include font-size(14px);
	margin-bottom: $margin-default /2;
	color: $color-gray-medium;
	font-family: $font-medium;
}

label,
.label {
	display: block;
	width: 100%;
	margin-bottom: $margin-default /2;
	+ em { // Error messages
		display: inline-block;

	}

@include media($screen-sm) {
	margin-bottom: $margin-default /2;
}

	&.mandatory:after {
		content: " *";
		color: $color-primary;
	}

	a {
		text-decoration: underline;
	}

}

// Errors
em.error {
	@include font-size(12px);
	position: relative;
	top: 3px;
	color: $color-error;
	font-style: normal;

}

select.error,
textarea.error,
input.error {
	border-color: $color-error !important;
}

input[type="radio"].error + label:before,
input[type="checkbox"].error + label:before {
	border-color: $color-error;
}

// Form elements
select,
textarea,
input[type="tel"],
input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"],
input[type="number"],
input[type="date"] {
	padding: $padding-btn;
	border: 1px solid $base-border-color;
	border-radius: $border-radius;
	background-color: $color-white;
	width: 100%;
}

select:focus,
textarea:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="email"]:focus,
input[type="search"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
input[type="date"]:focus {
	border: 1px solid $color-field-active;
}

// Radio + checkbox
input[type="radio"] + label,
input[type="checkbox"] + label {
	position: relative;
	float: left;
	margin-bottom: $margin-default;
	@include row();

@include media($screen-sm) { //if elements show beside
	clear: none !important;
}

}

input[type="radio"] + label:before,
input[type="checkbox"] + label:before {
	display: inline-block;
	content: "";
	float: left;
	width: 22px;
	height: 22px;
	margin-right: 8px;
	background-color: $color-white;
	border: 1px solid $base-border-color;
}

input[type="radio"]:focus + label:before,
input[type="checkbox"]:focus + label:before	{
	border: 1px solid $color-secondary;
}

input[type="radio"]:checked + label:after,
input[type="checkbox"]:checked + label:after {
	position: absolute;
	top: 3px;
	left: 4px;
	width: 18px;
	height: 18px;
	color: $color-secondary;
	font-family: $font-icon;
	border-radius: $border-radius;
}

input[type="radio"] + label span,
input[type="checkbox"] + label span {
	position: relative;
	top: 2px;
	hyphens: auto;
}

// Radio
input[type="radio"] + label:before {
	border-radius: 50%;
}

input[type="radio"]:checked + label:after {
	content: "\f111"; // fa-circle
}

input[type="radio"] + label span {
	margin-right: 10px;
}

// Checkbox
input[type="checkbox"] + label {
	position: relative;
}

input[type="checkbox"] + label:before {
	border-radius: $border-radius;
}

input[type="checkbox"]:checked + label:after {
	content: "\f00c"; // fa-check
}

input[type="radio"]+label.big-check,
input[type="checkbox"]+label.big-check {
	font-size: 36px;
	display: flex;
    align-items: center;
}

input[type="radio"]:checked+label.big-check:after,
input[type="checkbox"]:checked+label.big-check:after,
input[type="radio"]+label.big-check:before,
input[type="checkbox"]+label.big-check:before {
    left: 0px;
    width: 36px;
    height: 36px;
}

// Select
.select-wrap {
	position: relative;

	&:after {
		//@include font-size(20px);
		font-size: inherit;
		display: inline-block;
		position: absolute;
		top: 8px;
		right: 12px;
		content: "\f107"; // fa-angle-down
		color: $color-gray-dark;
		font-family: $font-icon;
		line-height: 1;
		pointer-events: none;
		speak: none;
	}

}

textarea {
	resize: none;
}

.contact-question-results {
	position: relative;
}

.form-row {
	@include row();

@include media($screen-sm) {
	display: flex;
	flex-wrap: wrap;
}

	&:last-child {
			margin-bottom: -$margin-default;
	}

}

.form-col {
	margin-bottom: $margin-default;

		&.right-end {
			text-align: right;
		}



	&.one {

	@include media($screen-sm) {
		@include span-columns(12);
	}

	}

	&.two {

	@include media($screen-sm) {
		@include span-columns(6);
		@include omega(2n);

	&.shift-right {
		@include shift(6);
	}

	}

	}

	&.three {

	@include media($screen-sm) {
		@include span-columns(4);
		@include omega(3n);

	&.shift-right {
		@include shift(8);
	}

	&.shift-middle {
		@include shift(4);
	}

	}

	}

	&.four {

	@include media($screen-sm) {
		@include span-columns(3);
		@include omega(4n);

	&.shift-right {
		@include shift(9);
	}

	}

	}

}

.form-row-inline {
	display: flex;
	flex-wrap: wrap;
}

.form-row-inline {
	&.right-end {
		justify-content: flex-end;
	}
}



.form-row-inline .form-col{
	flex: 1;

	&.endbutton {
		position: relative;
		>*{
			position: absolute;
			bottom: 1px;
			z-index: 1;
			right: 0px;
		}
	}
}





.form-row-inline {
	display: flex;
	flex-wrap: wrap;
}

.form-row-inline.right-end {
	justify-content: flex-end;
}



.form-row-inline {
	.form-col {
		&.fifty-of-four{
			flex: 0;
		    text-align: left;
		    min-width: 50%;
		    padding-right: 1.1rem;
		}
		&.col-number{
				max-width: 15%;
    		min-width: 10%;
		}


	}

}




.form-row-inline .form-col input.short-input{
	width: 40px;
	text-align: center;
}

.form-row-inline label, .form-row-inline .label {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.form-result-block {
	>ul {
		li {
			label { display: none;}
		}
		li:first-child {
			label { display: block;}
		}
	}
}
