////
/// gurka Coach
/// @group module
////

/// Edit

*[data-frontend="edit"]:not(:target) {display: none;}


.edit {

	article {
		margin-right: 0;
	}

	&-headline[data-frontend="panel-headline-all"].arrow-direction {
		//border-color: $color-secondary;
		//border-bottom: 0;
		//border-radius: $border-radius $border-radius 0 0;
		//margin-bottom: 0;
		color: $color-white;
		background: $color-secondary;
	}





	&-block {
			padding: $padding-default;
			//border: 1px solid #1485CC;
			border-radius: 0 0 $border-radius $border-radius;
			border-top: 0;
			margin-bottom: $margin-default;

			ul li {
				display: inline-block;
			}
	 	}

	&-list {

		li {
			 display: inline-block;
			 background: $color-white;
				border: 1px solid $color-gray-medium;
			 color: $color-secondary;

					border-radius: $border-radius;
					padding: $padding-btn;
					margin: 0 $margin-default/3 $margin-default/3 0;
					cursor: pointer;
					font-weight: normal;
			 @include font-size(13px);

			@include media($screen-sm) {
			 @include font-size(14px);
		 }
		}

	}

.choselist {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	overflow-x: scroll;
	margin-bottom: $margin-default /2;
	position: relative;

	@include media($screen-sm) {
		flex-wrap: nowrap;
	}

}

.chosewrapper {
	margin: $margin-default 0;
	border: 1px solid $color-gray-medium;
	border-radius: 0 0 4px 4px;
	padding: $margin-default $margin-default/2 $margin-default/2;
	overflow: hidden;

	label {
		display: inline-block;
		flex-grow: 1;
		flex-basis: 1;
		cursor: pointer;
		position: relative;
		min-width: 25%;
		margin-right: 2%;
	//	border: 1px solid #ccc;



	@include media($screen-sm) {
		min-width: 10%;
	}


		&:before,
		&:after {
			display: none !important;
		}


	}

	label.selected {
		border: 1px solid $color-secondary;
		order: 1;
	}
	label.unselected {
		order: 2;
	}

}


}
