

.btn {
	@extend .button;
	cursor: pointer;

  &.primary {
    color: $color-button-primary;
    //background-color: $color-secondary;
		background-color: $color-green;


    &.inverse {
      color: $color-primary;
      border: 1px solid $color-primary;
      background-color: $color-button-primary;
    }

  }

  &.secondary {
    color: $color-button-secondary;
    background-color: $color-primary;

    &.inverse {
      color: $color-secondary;
      border: 1px solid $color-secondary;
      background-color: $color-button-secondary;
    }

  }

  &.tertiary {
    color: $color-button-tertiary;
    background-color: $color-tertiary;

    &.inverse {
      color: $color-tertiary;
      border: 1px solid $color-tertiary;
      background-color: $color-button-tertiary;
    }

  }

  &:hover {
    opacity: $opacity-low;
  }

  &.inverse:hover {
    opacity: $opacity-medium;
  }

  // Hide all button icons
  &[class*="icon-"]:before {
    //display: none;
  }

  // Hide "remove" and "close" button
  &.icon-remove,
  &.icon-close {
    display: none;
  }

  &.icon-close {
    display: none;
  }

}


[data-action]:after  {
	font-family: "FontAwesome";

	@include font-size(13px);
	width: 20px;

@include media($screen-sm) {
	@include font-size(16px);
	width: 16px;
}

}

[data-action="rate"]:after  {
	content: '\f1e3';
}
[data-action="erase"]:after,
[data-action="quit-rate"]:after,
[data-action="quit-card"]:after,
[data-action="supererase"]:after,
[data-action="supererasecard"]:after,
[data-action="erase-match"]:after {
	content: '\f00d';
}
[data-action="next-rate"]:after  {
	content: '\f00c';
}

[data-action="back-rate"]:after  {
	content: '\f112';
}

[data-action="send-rate"]:after,
[data-action="send-card"]:after  {
	content: '\f090';
}

[data-action="supererase"],
[data-action="supererasecard"] {
display: inline-block;
}

[data-action="back-card"]:after  {
	content: '\f112';
}

[data-action="herocard"] {

}
[data-action="herocard"]:after {
	content: '\f091';
}
