

body {
	@include font-size(13px);
	background-color: $color-white;
	font-family: $font-base;
	color: $base-font-color;

@include media($screen-sm) {
	@include font-size(16px);
}

}

body >* {
	padding: 0 ($margin-gutter-xs /2) ;

@include media($screen-sm) {
	padding: 0 ($margin-gutter /2);
}

}



// Headlines
h1,
h2,
h3,
h4 {
	color: $color-secondary;
	font-family: $font-medium;
	width: 100%;
}

h1 {
	@include font-size(18px);

	@include media($screen-sm) {
		@include font-size(26px);
	}

	margin-bottom: $margin-default;
}

h2 {
	@include font-size(16px);

	@include media($screen-sm) {
		@include font-size(22px);
	}


	margin-bottom: $margin-default /2;
}

h3 {
	@include font-size(14px);

	@include media($screen-sm) {
		@include font-size(18px);
	}

	margin-bottom: $margin-default /2;
}

h4 {
	@include font-size(13px);

	@include media($screen-sm) {
		@include font-size(16px);
	}

	margin-bottom: $margin-default /2;
}

.center-headline {
text-align:	center;
}

//Panel-Headline
*[data-frontend="panel-headline"] {
	display: flex;
	padding: $padding-btn;
	align-items: center;
	text-decoration: none !important;
	border: 1px solid $color-gray-medium;
	border-radius: $border-radius;
	//color: $color-button-secondary;
	background-color: $color-white;

	&:after {
		display: inline-block;
		margin-left: auto;
		padding-left: 10px;
		content: "\f107";
		font-family: $font-icon;
		speak: none;
	}

	&.arrow-direction {
		&:after {
			content: "\f106";
		}
	}

	.order-2 > &,
	.order-1 > &{
		&:after {
			content: "\f106";
		}

		&.arrow-direction {
			&:after {
				content: "\f107";
			}
		}
	}

	@include media($screen-sm) {
		display: block;
		padding: 0;
		border: 0;
		border-radius: 0;
		background: transparent;
		color: $color-secondary;

		&:after {
			display: none;
		}
	}

	+ * {
		display: none;
	}

}

//Panel-Headline-All
*[data-frontend="panel-headline-all"] {
	display: flex;
	margin-bottom: $margin-default;
	padding: $padding-btn;
	align-items: center;
	text-decoration: none !important;
	border: 1px solid $color-gray-medium;
	//border-radius: $border-radius;
	background-color: $color-gray-light;
	cursor: pointer;
	@include font-size(13px);

	@include media($screen-sm) {
		@include font-size(15px);
	}

	&:after {
		display: inline-block;
		margin-left: auto;
		padding-left: 10px;
		content: "\f107";
		font-family: $font-icon;
		speak: none;
	}

	&.arrow-direction {
		color: $color-white;
		background: $color-secondary;
		&:after {
			content: "\f106";
		}
	}

& + * {
	display: none;
}

}

h3.matchside-headline span {
    margin: 0.375em;
    -ms-flex-pack: start;
    justify-content: flex-start;
    background: #ededed;
}

*[data-matchside] *[data-player] strong, h3.matchside-headline span {
    padding: 0.375em;
    text-align: center;
    color: #fff;
    background-color: #1485CC;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: default;
    margin: 0 0 0.5em 0;
    width: 100%;
    font-size: 16px;
    height: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}




// Inline
a {
	color: $color-primary;
	text-decoration: none;

	&:hover {
		color: $color-primary;
		text-decoration: underline;
	}

}

p {
	margin-bottom: $margin-default;

	&:last-child {
		margin-bottom: 0;
	}

}

dl,
ol,
ul,
table {
	margin-bottom: $margin-default;
}

li {
	margin-bottom: $margin-default /3;
}

em {
	font-style: italic;
}

strong {
	font-weight: bold;
}

small {
	@include font-size(10px);
}

hr {
	margin-bottom: $margin-default;
	border-top: 1px solid $color-secondary;
}

// Miscellaneous
::selection {
	background: $color-primary;
	color: $color-white;
}

// hide blocks after panelheadline for xs
aside,
article,
section{
	>*[data-frontend="panelheadline"] + * {
		display: none;

		@include media($screen-sm) {
			display: block !important;
		}
	}
}



//Extras

.versus-span {
	@extend .button;
}
