////
/// gurka navigation-xs
/// @group module
////

/// navigation-xs



.navigation-xs {
	@include outer-container;
	margin-bottom: $margin-default;

	@include media($screen-sm) {
		display: none !important;
	}

	.menu-trigger,
	.login-button {
		@extend .button;
		@include span-columns(2);
		@include clearfix();
		background: $color-secondary;
		width: $navi-trigger-width;

	&.logout {
		background: $color-primary;
	}
	}

	.menu-trigger:after,
	.login-button:after {
		position: relative;
		top: 2px;
		color: $color-white;
		font-family: $font-icon;
		speak: none;
		line-height: 1;
		display: inline-block;
	}

		.menu-trigger:after {
			content: "\f0c9"; // fa-bars
		}

		.login-button:after  {
				content: "\f007"; // fa-bars
		}


	.menu-trigger,
	.menu-trigger:after {
		//display: none !important; //if no navigation

	}

}









body.showmenu  {
	max-height: 100%;
	overflow-x: hidden;
	overflow-y: hidden;

	.navi-xs {
		.menu-trigger:after {
			content: "\f00d"; // fa-erase
		}

}


}

body >*:not(nav) {
	position: relative;
	left: 0;
	transition: left 0.5s;

}

body.showmenu >*:not(nav) {
	left: 73%;
	transition: left 0.5s;

	@include media($screen-sm) {
	left: 0;
	}

}

body.showmenu {
	nav.main {
		left: 0px;
		display: block;
		overflow: hidden;
		overflow-y: auto;
	}
}
