/// Cards


.cards {


	&-scale {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		overflow-x: scroll;
		margin-bottom: $margin-default /2;
		position: relative;


	@include media($screen-sm) {
		flex-wrap: nowrap;
	}

  &-container {
    margin: 0.5em 0;
    border: 1px solid $color-gray-medium;
    border-radius: 0 0 4px 4px;
    padding: $margin-default $margin-default/2 $margin-default/2;
    margin-top: calc(-#{$margin-default} - 2px);

    margin-bottom: $margin-default;
    border-top: 0;

    &-headline {
			margin: 1em 0;

			&:after {
				content: '';
				font-family: "FontAwesome";
				font-size: 16px;
				width: 16px;
				display: inline-block;
				margin-left: 10px;
				color: #c00;

			}

			&.headline-trophy:after {
				content: '\f091';
			}


		}


  }

		label {
			display: inline-block;
			flex-grow: 1;
			flex-basis: 1;
			cursor: pointer;
			position: relative;
			min-width: 60%;
			margin-right: 2%;
		//	border: 1px solid #ccc;



		@include media($screen-sm) {
			min-width: 25%;
		}


			&:before,
			&:after {
				display: none !important;
			}


		}

		label.selected {
			border: 1px solid $color-secondary;
			max-width: 35%;

		@include media($screen-sm) {
			max-width: 25%;
		}

		}

	}













}
