////
/// match-arrange
/// @group molecule
////

.match-arrange {

	&-headline[data-frontend="panel-headline-all"].arrow-direction {
		border: 1px solid $color-secondary;
		border-bottom: 0;
		//border-radius: $border-radius $border-radius 0 0;
		margin-bottom: 0;
	}

	.player-select {
		padding: $padding-default;
		border: 1px solid $color-secondary;
		background: $color-gray-light;
		border-top: 0;
		overflow-y: auto;
		max-height: 80px;

		ul li {
			display: inline-block;
		}

		*[data-action] {
			display: none;
		}
	}


		.false4-alert {
			display: none;
		}

		.false4 {
			.false4-alert {
				display: block;
			}

			ul {
				display: none;
			}

	}


}
