////
/// gurka result
/// @group module
////

/// Result

.result {
	@include row();



	.result-list {

		li {
			margin-bottom: $margin-default;
			width: auto;
			float: left;
			width: 100%;
			align-items: center;
			display: flex;
			flex-wrap: wrap;
			padding: $padding-vertical-default;
			border-bottom: 1px solid $color-gray-medium;
		}

		div {
			display: inline-block;
			flex: 1;
			order: 2;
		&.game {
			text-align: left;
			flex: 2;
			flex-basis: 25%;
			max-width: 40%;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			padding: 0 4px;

			>span {
				width: 60%;
				max-width: 60%;
				display: inline-block;
				white-space: nowrap;
				overflow: hidden;
				//text-overflow: ellipsis;

				@include media($screen-sm) {
				max-width: 33%;
				width: auto;
				}

			}

			> span ~ strong {
				margin: 0 3px;
				font-weight: normal;
			}

			> strong:last-child {
				display: none;
			}


		}

		}
[data-action="erase-match"]:after {
	color: $color-primary;
	background-color: transparent;
}

	}

	.arrow-direction + .result-list {
		display: block;
	}
		


	menu {

		margin-bottom: $margin-default;

		span {
			display: none;
		}





	}

	input[type="radio"] {
		display: none;
	}


	label[for$="Results"] {
		@extend .button;
		@extend .button.secondary.inverse;
		cursor: pointer;
	}







	&-row,
	.played,
	.ordered {
		@include row();
		display: flex;
		flex-wrap: wrap;
		@include font-size(12px);
		@include media($screen-sm) {
			@include font-size(16px);
		}

		&:last-child {
			margin-bottom: -$margin-default;
		}

	}

	.ordered {
		display: none;
	}

	&-col {
		margin-bottom: $margin-default;
		width: auto;
		float: left;


		&.one {
			@include span-columns(12);
			padding: $padding-vertical-default;
			border-bottom: 1px solid $color-gray-medium;

			@include media($screen-sm) {
				@include span-columns(12);
			}



		}

		&.two {

			@include media($screen-sm) {
				@include span-columns(6);
				@include omega(2n);

				&.shift-right {
					@include shift(6);
				}

			}

		}

		&.three {

			@include media($screen-sm) {
				@include span-columns(4);
				@include omega(3n);

				&.shift-right {
					@include shift(8);
				}

				&.shift-middle {
					@include shift(4);
				}

			}

		}

		&.four {

			@include media($screen-sm) {
				@include span-columns(3);
				@include omega(4n);

				&.shift-right {
					@include shift(9);
				}

			}

		}

		.result-list & {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}

	}




}
