////
/// gurka header
/// @group module
////

/// Main header

body > header {
	padding: 0;
	overflow: hidden;
	background-color: $color-white;


	.wrapper {
		@include outer-container;
		@include clearfix();
		height: auto;
		margin-bottom: $margin-default;
		padding-top: 0px;
		text-align: center;

	@include media($screen-sm) {
		height: $header-wrapper-height;
	}


		//background-image: url(../assets/images/header.png);
		background-size: cover;

		.header-link {

			display: block;
			margin: 0 auto;
			text-align: center;


		}

		img {

			max-height: $header-wrapper-height;
			width: auto;
			max-width: 100%;
			display: inline-block;

		@include media($screen-sm) {
			//@include span-columns(3);
			max-height: $header-wrapper-height;
		}

		}


		h2 {
			@include span-columns(9);
			@include shift(0);
			color: $color-white;
		}

		p {
			display: none;

		@include media($screen-sm) {
			@include span-columns(12);
			@include shift(0.5);
			display: block;
			color: $color-white;
		}

		}

	}

}
