////
/// gurka table
/// @group module
////

/// Table

.table {
	@include row();
	overflow: hidden;
	@include font-size(13px);
	@include media($screen-sm) {
		@include font-size(16px);
	}


	table,
	tbody {
		width: auto;
		border-collapse: collapse;
		overflow-x: scroll;
		display: block;

		@include media($screen-sm) {
			width: 100%;
			overflow-x: hidden;
		}
	}

	table {
		border-bottom: 1px solid $color-gray-medium;
		margin-bottom: $margin-default;
		padding-bottom: $padding-default;
	}

	tbody {
	}


		tr {
			display: flex;
			flex-basis: 100%;
			align-items: center;

			&:nth-child(2n) {
				td {
					background: $color-gray-light;
				}
			}

			td,
			th {
				flex: 1;
				padding: 5px;
				min-width: 35px;
				text-align: center;
				line-height: 1.5;
				padding: 2px;

				&:nth-child(1) {
					flex: 0;
					width: 40px;
					border: none;
					background: transparent;
				}


				&:nth-child(2) {
					position: relative;
					flex: 2;
					text-align: left;
					width: 40%;
					padding-right: 1.1rem;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;

					span:before {
						content: "(";
					}

					span:after {
						content: ")";
					}
				}

				&.small {
					display: none;

				@include media($screen-sm) {
					display: table-cell;
				}

				}


			}

			td {
				&:nth-child(1) {
					@extend .design-col-table-a;
				}
			}


		}

	thead {
		@include font-size(11px);
		display: flex;
		width: auto;
		border-bottom: 1px solid $color-secondary;
		flex-wrap: wrap;
		flex-basis: 100%;

		@include media($screen-sm) {
			width: 100%;
		}

		tr {
			display: flex;
			flex-basis: 100%;

		}


		th {
			flex: 1;
			padding: 5px;
			min-width: 35px;
			text-align: center;
			line-height: 1.5;
			padding: 2px;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;

			&:nth-child(1) {
				flex: 0;
				width: 40px;
				border: none;
				background: transparent;
			}


			&:nth-child(2) {
				position: relative;
				flex: 2;
				text-align: left;
				width: 40%;
				padding-right: 1.1rem;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;

			}

			&.small {
				display: none;

			@include media($screen-sm) {
				display: table-cell;
			}

			}


		}

	}

	tfoot {
		@include font-size(11px);
		display: flex;
		width: auto;
		border-top: 1px solid $color-secondary;
		flex-wrap: wrap;
		flex-basis: 100%;

		@include media($screen-sm) {
			width: 100%;
		}

		tr {
			display: flex;
			flex-basis: 100%;

			td {
				flex: 1;
				padding: 5px;
				color: $color-gray-medium;
			}
		}

	}


	td.table-strake,
	th.table-strake {
		display: none;
		justify-content: center;
	}

	@include media($screen-sm) {
		td.table-strake,
		th.table-strake {
			display: flex;
		}
	}

}
