
.layout {
	@include row();
	display: flex;
	flex-wrap: wrap;
@include media($screen-sm) {
	display: block;
}

	aside,
	.aside-like  {
	@include span-columns(12);
	@include omega();

@include media($screen-sm) {
	@include span-columns(3);
}

	margin-bottom: $margin-default *2;

		>:last-child {
			margin-bottom: 0;
		}

	&.order-1 {
		order: 1;

		>ul,
		>ol {
			display: block;
		}
	}

	&.order-2 {
		order: 2;

		>ul,
		>ol {
			display: block;
		}
	}

	&.order-3 {
		order: 3;
	}

	}

	article,
	.article-like {
	@include span-columns(12);
	@include omega();


@include media($screen-sm) {
	@include span-columns(9);
}

	margin-bottom: $margin-default *2;

		>:last-child {
			margin-bottom: 0;
		}

	&.order-1 {
		order: 1;

		>ul,
		>ol {
			display: block;
		}
	}

	&.order-2 {
		order: 2;

		>ul,
		>ol {
			display: block;
		}
	}

	&.order-3 {
		order: 3;
	}

	}

	&.top {

		aside,
		.aside-like  {

	@include media($screen-sm) {
		@include span-columns(12);
	}



		.accordion-tabs > li {
			float: none;
			margin-right: 0;
		}

		}

		article,
		.article-like {

		@include media($screen-sm) {
			@include span-columns(12);
		}

		}

	}

	&.right {

		aside,
		.aside-like  {

		@include media($screen-sm) {
			@include shift(9);
		}

		}

		article,
		.article-like {

		@include media($screen-sm) {
			@include shift(-12);
		}

		}

	}

	&.bottom {

		aside,
		.aside-like  {

		@include media($screen-sm) {
			@include span-columns(12);
		}

			li {
				float: left;
				margin-right: 10px;
			}

			.accordion-tabs > li {
				float: none;
				margin-right: 0;
			}

		}

		article,
		.article-like {

		@include media($screen-sm) {
			@include span-columns(12);
		}

		}

	}

}

// Temporarily placed here...
main {
	@include outer-container;
	clear: both;
	display: flex;
	flex-wrap: wrap;

@include media($screen-sm) {
	display: block;
}

	>*{
		width: 100%;

	&.order-2 {
		order: 2;

	}

	&.order-3 {
		order: 3;
	}

	}
}




.intro,
.main-container {
	@include row();
}

.teaser {
	@include clearfix();
}

.main-aside {

@include media($screen-sm) {
	@include span-columns(3);
	@include shift(9);
}

}

.main-article {

@include media($screen-sm) {
	@include span-columns(9);
	@include shift(-12);
}

}

// Teaser-row
.teaser-flex {
	display: flex;
	flex-wrap: wrap;

	.item-header {
		width: 100%;
	}
	.item {
		display: flex;
		flex-wrap: wrap;
	@include media($screen-xs) {
		flex: 1 1 50%;
	}
		flex: 1;
	}
}

.modules-headline {
	text-decoration: underline;
	color: $color-primary;
}

.atoms,
.molecules,
.modules {



	&-element {
		@include row();
		margin-bottom: $margin-default *3;

		&:last-child {
			border-bottom: 1px solid $color-complementary;
			padding-bottom: $padding-default;
		}

	}
}
