////
/// matchside
/// @group molecule
////

.matchside {

	&-headline {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		align-items: center;
		margin-bottom: 0;

		&.headline-home {
			flex-direction: row;
		}
		&.headline-visitor {
			flex-direction: row-reverse;

			span {
				justify-content: flex-end;
			}

		}

		span{
			margin: $margin-default/2;
			justify-content: flex-start;
			background: $color-gray-light;
		}

	}

	.goals-input {
		width: 60px;
		margin: $margin-default/2;
		text-align: center;
		color: $color-secondary;
		background: $color-gray-light !important;
		display: none;

		&:disabled {
			background: $color-complementary !important;
			color: $base-font-color;
		}
	}

	*[data-matchside] *[data-action] {
		display: flex;
		align-items: center;
		cursor: pointer;
		margin: 0 $margin-default/3;
		padding: $padding-default/2;
		height: 30px;
	}

	*[data-action="erase"] {
		position: absolute;
	}


	*[data-matchside="Home-Players"] *[data-action="erase"]{
		right: 0px;
	}

	*[data-matchside="Visitor-Players"] *[data-action="erase"]{
		left: 0px;
	}

	.player-list {

		margin-top: $margin-default /2;

		li[data-player] {
			width: 100%;
			display: flex;
			justify-content: flex-end;
			flex-wrap: wrap;
			border: 1px solid $color-gray-medium;
			//border-radius: $border-radius;
			position: relative;
			background: $color-gray-light;
			margin-bottom: $margin-default;
			position: relative;

			@include media($screen-sm) {
				flex-wrap: nowrap;
			}
		}

		&[data-matchside="Home-Players"] *[data-player] {
			flex-direction: row-reverse;
			padding: $margin-default/3 36px $margin-default/3 $margin-default/3;

			strong {
				justify-content: flex-end;
			}
		}

		&[data-matchside="Visitor-Players"] *[data-player] {
			flex-direction: row;
			padding: $margin-default/3 $margin-default/3 $margin-default/3 36px;

			strong {
				justify-content: flex-start;
			}
		}


	}






}
