
.box-a {

@include media($screen-sm) {
	@include span-columns(8);
}

}

.box-b {

@include media($screen-sm) {
	@include span-columns(4);
}

}

.box-c {

@include media($screen-sm) {
	@include span-columns(1 of 4);
}
	background: red;
}

.col-100 {
	@include span-columns(12);
	background: #1abc9c;
}

.col-50 {

@include media($screen-sm) {
	@include span-columns(6);
	@include omega(2n);
}

	background: #3498db;
}

.col-66 {

@include media($screen-sm) {
	@include span-columns(8);
}

	background: #f1b8e7;
}

.col-33 {

@include media($screen-sm) {
	@include span-columns(4);
	@include omega(3n);
}

	background: #eee;
}

.col-25 {


@include media($screen-sm) {
	@include span-columns(3);
	@include omega(4n);
}
	background: #f1c40f;
}

.col-16 {

@include media($screen-sm) {
	@include span-columns(2);
	@include omega(6n);
}

	background: #e74c3c;
}

.col-nested-50 {

@include media($screen-sm) {
	@include span-columns(4 of 8);
}

	background: #e9f802;
}

.col-nested-25 {

@include media($screen-sm) {
	@include span-columns(1.5 of 6);
	//@include omega(4n);
}

	background: #f1c40f;

}

.col-nested-16 {

@include media($screen-sm) {
	@include span-columns(1 of 6);
	//@include omega(6n);
}

	background: #05507e;
}
